import s from "../Kafedra/Kafedra.module.css";
import {useEffect, useState} from "react";
import {_axios} from "../../axios/_axios";
import {Link} from "react-router-dom";

const Kafedra = () => {
  const [data, setData] = useState();
  const [bufferImg, setBufferImg] = useState();
  useEffect(
    () => {
      _axios.get('/new/5fbc4a252883561028540508')
        .then(({data}) => {
          setData(data)
          setBufferImg(Buffer.from(data.gallery.data, 'base64').toString('base64'))
        })
        .catch(e => {
          console.log(e)
        })
    }, [])
  const arr = [1, 2, 3, 4, 5]
  return (
    <div>
      <h1 className={s.main_title}>–ù–∞—à—ñ –≤–∏–∫–ª–∞–¥–∞—á—ñ</h1>
      <div className={s.main}>
        {data && arr.map(person =>
          <div key={person}>
            <div className={s.person}>
              <Link className={s.name} to={'/person/:id'}>
                <img alt={'person'} src={`data:${data?.gallery?.contentType};base64,${bufferImg}`}/>
              </Link>
              <div className={s.text}>
                <Link className={s.name} to={'/person/:id'}><h3>Test Title</h3></Link>
                <p><strong>–í—á–µ–Ω–µ –∑–≤–∞–Ω–Ω—è: </strong>{arr}</p>
                <p><strong>–ù–∞—É–∫–æ–≤–∏–π —Å—Ç—É–ø—ñ–Ω—å: </strong>{arr}</p>
              </div>
            </div>
            <hr/>
          </div>
        )}
      </div>
    </div>
  );
}

export default Kafedra;
