import React, {useState} from "react";
import s from './Admin.module.css';
import {Editor} from '@tinymce/tinymce-react';
import FileBase64 from 'react-file-base64';
import {_axios} from "../../axios/_axios";

const Admin = () => {
  const [page, setPage] = useState({desc: '', title: '', gallery: []});

  const handleEditorChange = (content, editor) => {
    setPage({...page, desc: content})
  }
  const handleTitle = ({target}) => {
    setPage({...page, title: target.value})
  }
  const handleGallery = (files) => {
    console.log(files)
    if (files.length) {
      setPage({...page, gallery: files[0].base64, type: files[0].type})
    }
  }
  const handleSend = () => {
    _axios.post('/new', page, {headers: {authorization: 'bear '}}).then(res => {
      console.log(res)
    })
  }

  return (
    <div>
      <h2 className={s.title}>
        –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –ù–æ–≤–∏–Ω
      </h2>
      <div className={s.container}>
        <h3>–ó–∞–≥–æ–ª–æ–≤–æ–∫</h3>
        <input type={'text'} name={'title'} onChange={handleTitle}/>
        <h3>–§–æ—Ç–æ</h3>
        <FileBase64 multiple={true} onDone={handleGallery}/>
        <img className={s.img_view} src={page.gallery} alt={'photos'}/>
        <h3>–û–ø–∏—Å</h3>
        <Editor
          initialValue=""
          init={{
            height: 500,
            menubar: false,
            plugins: [
              'advlist autolink lists link image charmap print preview anchor',
              'searchreplace visualblocks code fullscreen',
              'insertdatetime media table paste code help wordcount'
            ],
            toolbar: 'undo redo | formatselect | bold italic backcolor | \
              alignleft aligncenter alignright alignjustify | \
              bullist numlist outdent indent | removeformat | help'
          }}
          onEditorChange={handleEditorChange}
        />
      </div>
      <button className={s.btn} onClick={handleSend}>–°—Ç–≤–æ—Ä–∏—Ç–∏</button>
    </div>
  );
}

export default Admin;
