import React from "react";
import s from "./MobileMenu.module.css";
import CloseIcon from '@material-ui/icons/Close';
import HomeIcon from "@material-ui/icons/Home";
import {Link} from "react-router-dom";

const MobileMenu = ({isOpen, setIsOpen}) => {
  const handleClose = () => {
    setIsOpen(!isOpen)
  }

  return !!isOpen && (
    <div className={s.burger_container}>
      <h2 className={s.header}>–ú–µ–Ω—é <CloseIcon onClick={handleClose} className={s.close}/></h2>
      <div className={s.list_container}>
        <div className={s.menu}>
          <h3 className={s.link_container}>
            <HomeIcon className={s.home}/>
            <Link onClick={handleClose}  className={s.link} to={''}>
              –ì–æ–ª–æ–≤–Ω–∞
            </Link>
          </h3>
          <h3 className={s.link_p}>
            <Link onClick={handleClose}  className={s.link} to={'/kafedra'}>–ü—Ä–æ –∫–∞—Ñ–µ–¥—Ä—É</Link>
          </h3>
          <h3 className={s.link_p}>
            <Link onClick={handleClose}  className={s.link} to={'/vstupnykam'}>–í—Å—Ç—É–ø–Ω–∏–∫–∞–º</Link>
          </h3>
          <h3 className={s.link_p}>
            <Link onClick={handleClose}  className={s.link} to={'/process'}>–ù–∞–≤—á–∞–ª—å–Ω–∏–π –ø—Ä–æ—Ü–µ—Å</Link>
          </h3>
          <h3 className={s.link_p}>
            <Link onClick={handleClose}  className={s.link} to={'/science'}>–ù–∞—É–∫–∞</Link>
          </h3>
          <h3 className={s.link_p}>
            <Link onClick={handleClose}  className={s.link} to={'/news'}>–ù–æ–≤–∏–Ω–∏</Link>
          </h3>
          <h3 className={s.link_p}>
            <Link onClick={handleClose}  className={s.link} to={'/blog'}>–ë–ª–æ–≥</Link>
          </h3>
          <h3 className={s.link_p_last}>
            <Link onClick={handleClose}  className={s.link} to={'/reviews'}>–í—ñ–¥–≥—É–∫–∏ –µ–∫–æ–Ω–æ–º—ñ–∫–∏ –ó–£–ù–£</Link>
          </h3>
        </div>
      </div>
    </div>
  )
}

export default MobileMenu;
