import React from "react";
import s from "./Header.module.css";
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import YouTubeIcon from '@material-ui/icons/YouTube';
import EmailIcon from '@material-ui/icons/Email';
import {TextField} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import {withStyles} from "@material-ui/core/styles";
import {Link, useHistory} from "react-router-dom";

const Header = () => {
  const h = useHistory();
  const CssTextField = withStyles({
    root: {
      '& .MuiOutlinedInput-root': {
        borderRadius: '0',
        '& fieldset': {
          borderColor: '#3b5998',
        },
        '&:hover fieldset': {
          borderColor: '#3b5998',
        },
      },
    },
  })(TextField);

  const handleSearch = () => {
    h.push('/news')
  }

  return (
    <header className={s.header_container}>
      <div className={s.header}>
        <div className={s.img_title}>
          <div className={s.logo}>
            <Link to={'/'}>
              <img src={'http://marketing.tneu.edu.ua/wp-content/uploads/LOGO-TNEU-100x100-1.png'} alt={'logo'}/>
            </Link>
          </div>
          <div className={s.title}>
            <Link to={'/'} className={s.link}>
              <h2 className={s.name}>
                –ö–∞—Ñ–µ–¥—Ä–∞ –µ–∫–æ–Ω–æ–º—ñ–∫–∏ —Ç–∞ –µ–∫–æ–Ω–æ–º—ñ—á–Ω–æ—ó —Ç–µ–æ—Ä—ñ—ó
              </h2>
            </Link>
          </div>
        </div>
        <div className={s.search}>
          <div className={s.search_container}>
            <CssTextField
              label={'–ü–æ—à—É–∫'}
              variant="outlined"
              size={'small'}
              InputProps={{
                endAdornment: <SearchIcon onClick={handleSearch} className={s.search_icon}/>
              }}
            />
          </div>
        </div>
        <div className={s.social}>
          <p className={s.social_title}>–ú–∏ –≤ —Å–æ—Ü–º–µ—Ä–µ–∂–∞—Ö:</p>
          <a href={'https://www.facebook.com'}>
            <FacebookIcon className={s.fb}/>
          </a>
          <a href={'https://www.instagram.com'}>
            <InstagramIcon className={s.insta}/>
          </a>
          <a href={'https://www.youtube.com'}>
            <YouTubeIcon className={s.youtube}/>
          </a>
          <a href={'mailto:kaf_et@wunu.edu.ua'}>
            <EmailIcon className={s.email}/>
          </a>
        </div>
      </div>
    </header>
  )
}

export default Header;
